import React from 'react'
import { nullToNA } from '../../../../Components/Common/PowerUps/PowerupFunctions'

function ReviewSelfAdvertForm(props) {

    const { setFormIndex } = props?.values
    let labelStyle = "mt-6 -ml-7 text-md text-gray-600  font-bold"
    let inputStyle = "mt-6 -ml-7 text-sm text-gray-600"

    const submitForm = () => {
        props.submitFun()
    }
    console.log("data in review form...", props?.allFormData)
    console.log("data in review form document...", props?.reviewIdNameData)

    return (
        <>
            <div className='absolute w-full ml-4 top-4 '>
                <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto  '>
                    <div className='col-span-8 border border-dashed border-violet-800 '>
                    <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Application Details</h1>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>License Year</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.licenseYear || props?.reviewIdNameData?.selfAdvertisement?.license_year_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Applicant</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.applicantName  || props?.reviewIdNameData?.selfAdvertisement?.applicant)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Father Name </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.fatherName  || props?.reviewIdNameData?.selfAdvertisement?.father)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>E-mail </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.email  || props?.reviewIdNameData?.selfAdvertisement?.email)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Residence Address</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.residenceAddress || props?.reviewIdNameData?.selfAdvertisement?.residence_address)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Residence Ward No </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.residenceWardNo || props?.reviewIdNameData?.selfAdvertisement?.ward_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Permanent Address</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.permanentAddress || props?.reviewIdNameData?.selfAdvertisement?.permanent_address)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Permanent Ward No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.permanentWardNo || props?.reviewIdNameData?.selfAdvertisement?.permanent_ward_name)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>Mobile No.</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.mobileNo || props?.reviewIdNameData?.selfAdvertisement?.mobile_no)}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>Aadhar No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.aadharNo || props?.reviewIdNameData?.selfAdvertisement?.aadhar_no)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Name</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.entityName || props?.reviewIdNameData?.selfAdvertisement?.entity_name)}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Address</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.entityAddress || props?.reviewIdNameData?.selfAdvertisement?.entity_address)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Ward No.</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.entityWardNo || props?.reviewIdNameData?.selfAdvertisement?.entity_ward_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Installation Location</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.installationLocation || props?.reviewIdNameData?.selfAdvertisement?.installation_location)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Brand Display Name</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.brandDisplayName || props?.reviewIdNameData?.selfAdvertisement?.brand_display_name)}</span>
                            </div><div className=''>
                                <p className={`${labelStyle}`}>Holding No.</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.holdingNo || props?.reviewIdNameData?.selfAdvertisement?.holding_no)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>Trade License No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.tradeLicenseNo || props?.reviewIdNameData?.selfAdvertisement?.trade_license_no)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>GST No.</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.gstNo || props?.reviewIdNameData?.selfAdvertisement?.gst_no)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Display Area</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.displayArea || props?.reviewIdNameData?.selfAdvertisement?.display_area)}</span>
                            </div><div className=''>
                                <p className={`${labelStyle}`}>Display Type</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.displayType || props?.reviewIdNameData?.selfAdvertisement?.display_type)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Advertisement Category</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.advrtCategory || props?.reviewIdNameData?.selfAdvertisement?.advt_category_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Longitude</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.longitude || props?.reviewIdNameData?.selfAdvertisement?.longitude)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Latitude</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.selfAdvertisement?.latitude || props?.reviewIdNameData?.selfAdvertisement?.latitude)}</span>
                            </div>

                        </div>
                    </div>
                    {/* document */}
                    <div className='col-span-4 border border-dashed border-violet-800 '>
                    <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Documents Uploaded</h1>

                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2 ml-12'>
                            {props?.reviewIdNameData?.selfAdvertisementDoc?.map((data, index) => (
                                <div className=''>
                                    <>
                                        <p className={`${labelStyle}`}>{data?.docCode} </p>
                                        <div className='flex -mt-2'>
                                            <span className={`${inputStyle} flex-1`}>{data?.image?.name}</span>
                                            <span className={`mt-4  flex-1 float-right`}><img src="https://cdn-icons-png.flaticon.com/512/1829/1829589.png" alt="Preview Image" className={` w-8`} /></span>
                                        </div>
                                    </>
                                </div>
                            ))}

                        </div>
                    </div>
                </div>

                <div className="grid grid-cols-12 w-full p-3">
                    <div className='md:pl-0 col-span-6'>
                        <button type="button" class="text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0" onClick={() => setFormIndex(2)}>Back</button>
                    </div>
                    <div className='col-span-6'>
                        <button type="submit" class="float-right text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-green-600 focus:bg-green-600 focus:border-green-600 focus:outline-none focus:ring-0" onClick={submitForm}>Submit</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ReviewSelfAdvertForm